.header-container{
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 1rem;
  margin: 8rem auto 8rem auto;
}

.row-container {
  display: flex;
  align-items: center;
  justify-content: center;
  object-fit: contain;
  flex-wrap: wrap;
  gap: 5rem;
  margin-bottom: 50px;
}

.entry-container {
  display: flex;
  flex-direction: column;
  flex: 1.5;
  padding: 20px;
  align-items: flex-start;
  justify-content: center;
}

.img-container {
  display: flex;
  flex: 1;
  align-items: center;
  justify-content: center;
  padding: 1rem;
}

.introduction {
  font-size: 80px;
  text-align: left;
  margin: 0;
}

h2 {
  color: #939db1;
  text-align: left;
  font-size: 40px;
  margin-top: 20px;
}

.headshot-img {
  height: 400px;
  border-radius: 20px;
}

@media only screen and (max-width: 1100px) {
  .row-container{
    flex-direction: column;
    gap: 2rem;
  }
  .headshot-img {
    display: none;
  }
  .header-container {
    margin: 1rem auto 5rem auto;
  }
}
